body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.helper {
	padding-top: 3em;
	width: 90%;
	margin: 0 auto;
	margin-bottom: 3em;
}

.helper h1,
.helper h3 {
  text-align: center;
  vertical-align: middle;
}

.helper .time-entry {
	margin-bottom: 1em;
	margin-top: 1em;
}

.helper .form-control {
	text-align: center;
	font-size: 3em;
	height: 2em;
}

.home {
	margin-top: 2em;
	background-color: #3782c4;
}

.away {
	margin-top: 2em;
	background-color: #d9534f;
}

.v-align {
	float: none;
	display: inline-block;
	vertical-align: middle;
}

.title {
  margin-top: 1em;  
}

.instructions {
	padding: 1em;
	text-align: center;
}

.tap-button {
	margin: 0 auto;
	width: 80%;
}
